
import Vue from 'vue';
import { mapMutations } from "vuex"
export default {
	onLaunch: function() {
		console.log('App Launch');
		uni.getSystemInfo({
			success: function(e) {
				// console.log(e)










				Vue.prototype.StatusBar = e.statusBarHeight;
				let custom = wx.getMenuButtonBoundingClientRect();
				Vue.prototype.Custom = custom;
				Vue.prototype.CustomBar = custom.bottom + custom.top - e.statusBarHeight;






				Vue.prototype.SystemInfo = {
					platform: e.platform,
					windowWidth: e.windowWidth,
					windowHeight: e.windowHeight,
					screenWidth: e.screenWidth,
					screenHeight: e.screenHeight
				}
			}
		})
		
		// 判断app接下来的入口
		let storageInfo = uni.getStorageInfoSync();
		if (storageInfo.keys.indexOf('token') != -1) {
			uni.getStorage({
				key: 'token',
				success:(res)=>{
					this.changeVal({stateKey: 'token', newValue: res.data});
					if (storageInfo.keys.indexOf('firstPerfectInfo') != -1 && uni.getStorageSync('firstPerfectInfo')) {
						// 一般直接进入首页-home/index;
						uni.reLaunch({
							url: 'pages/home/index',
						})
					} else {
						uni.reLaunch({
							url: 'pages/initial/personaldata',
						})
					}
				}
			})
		} else {
			uni.reLaunch({
				url: 'pages/initial/wxoauth',
			})
		}
	},
	onShow: function() {
		
	},
	onHide: function() {
		
	},
	methods: {
		...mapMutations(['changeVal']),
	}
}
